<template>
  <div>
    <div class="area_fold" 
      v-for="(newFieldInfo, index) in viewModel.newFieldInfoView"
      :key="index">
      <div class="head_fold">
        <strong class="tit_fold">{{ newFieldInfo.foldTitle }}</strong>
        <div v-if="newFieldInfo.foldRight" class="area_right">
          <template
            v-for="(foldRightItem, foldRightIndex) in newFieldInfo.foldRight"
            :key="foldRightIndex">
            <component
              v-if="foldRightItem.componentName == 'Button'"
              :is="foldRightItem.componentName"
              :btnSize="foldRightItem.btnSize"
              :btnStyle="foldRightItem.btnStyle"
              :text="foldRightItem.text"
              @onClickBtn="foldRightItem.onClickBtnFunction()">
            </component>
            <Button
              v-if="foldRightItem.isCustom && foldRightItem.customName == 'managerAddBtn'"
              btnSize="small"
              btnStyle="secondary"
              text="실무자추가"
              @onClickBtn="viewModel.onAddManager()" />
          </template>
        </div>
      </div>
      <div class="body_fold">
        <template
            v-for="(foldData, foldDataIndex) in newFieldInfo.foldDataList"
            :key="foldDataIndex">
          <dl
            v-if="!foldData.isCustom"
            :class="['list_table list_table_w', foldData.halfAlign ? 'list_table_half list_table_half_'+foldData.halfAlign : '' ]">
            <dt>{{ foldData.title }}<span v-if="foldData.isEssential" class="ico_zippro ico_star">필수항목</span></dt>
            <dd>
              <component
                v-if="foldData.componentName == 'Input' || 'InputUnit' || 'InputSelectNumber' || 'InputSearch'"
                :is="foldData.componentName"
                :unit="foldData.unit"
                :maxValue="foldData.maxValue"
                :placeholder="foldData.placeholder"
                v-model:value="viewModel.model[foldData.value]"
                @onSearch="value => onSearch(value)">
              </component>
              <div
                v-if="foldData.isHasMap && viewModel.isShowMap"
                id="map"
                class="area_map">
                <span class="badge_point">
                  <span class="txt_point">현장위치</span>
                  <span class="icon_point"></span>
                </span>
              </div>
              <p v-if="foldData.descTip" class="desc_tip"><span class="ico_zippro ico_exclam"></span>{{ foldData.descTip }}</p>
            </dd>
            <!-- <dd v-else>{{ valueView(newFieldInfo) }}</dd> -->
          </dl>
          <template v-else>
            <template
              v-for="( managerInfo, managerIndex ) in viewModel.model.managerInfoList"
              :key="managerIndex">
              <div :class="['group_table',{ 'group_table_button' : managerIndex > 0}]">
                <strong class="tit_group">실무자{{ managerIndex+1 }}</strong>
                <dl class="list_table list_table_w list_table_half list_table_half_l">
                  <dt>이름</dt>
                  <dd>
                    <Input
                      placeholder="실무자1 이름을 입력해주세요"
                      v-model:value="managerInfo.managerName" />
                  </dd>
                </dl>
                <dl class="list_table list_table_w list_table_half list_table_half_r">
                  <dt>연락처</dt>
                  <dd>
                    <Input
                      placeholder="- 없이 입력"
                      v-model:value="managerInfo.managerPhone" />
                  </dd>
                </dl>
                <div v-if="managerIndex > 0" class="area_right">
                  <Button
                    btnSize="small"
                    btnStyle="tertiary"
                    text="삭제"
                    @onClickBtn="viewModel.onDeleteManager(managerInfo)" />
                </div>
              </div>
            </template>
          </template>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import BoxCheckbox from '@/components/common/checkbox/BoxCheckbox';
import Input from '@/components/common/input/Input';
import InputSearch from '@/components/common/input/InputSearch';
import InputSelectNumber from '@/components/common/input/InputSelectNumber';
import InputUnit from '@/components/common/input/InputUnit';
import Button from '@/components/common/button/Button';

export default {
  name:'NewFieldWrite',
  components:{
    BoxCheckbox,
    Input,
    InputSearch,
    InputSelectNumber,
    InputUnit,
    Button
  },
  props:{
    viewModel: Object,
  },
  methods:{
    onSearch(value){ 
      this.viewModel.onSearchAddress(value);
     },
    valueView(newFieldInfo){
      var value = this.viewModel.model[newFieldInfo.value];
      if(!value || value.length == 0) return '-';
      const type = typeof(value);
      if( newFieldInfo.dataList ){
        var dataList = JSON.parse(JSON.stringify(this.viewModel.[newFieldInfo.dataList]));
        if(type != 'string'){
          value = JSON.parse(JSON.stringify(value));
          const valueDataList = dataList.filter(item => value.includes(item.id)).map(item => item.text);
          return valueDataList.join(', ');
        }else{
          return dataList.find(item => item.id == value).text;
        }
      }
      switch(type){
        case 'string':{
          return `${value}${newFieldInfo.unit ? newFieldInfo.unit : ''}`;
        }
        case 'number':{
          return `${addCommaForMoney(value)}${newFieldInfo.unit ? newFieldInfo.unit : ''}`;
        }
      }
    }
  }
}
</script>
<style scoped>
.area_map{overflow:hidden;background:url(/assets/images/img_map.png) 50%;cursor:grab}
.area_map{position:relative;height:200px;margin-top:8px;border:1px solid #e1e1e1}

/* .area_map:before,
.area_map:after{position:absolute;background-color:rgba(237,22,75,0.2);content:''}
.area_map:before{top:50%;left:0;width:100%;height:1px}
.area_map:after{left:50%;top:0;height:100%;width:1px} */

.badge_point{position:absolute;top:50%;left:50%;margin:-38px 0 0 -28px}
.badge_point .txt_point{display:block;width:57px;height:25px;padding-top:5px;border-radius:2px;background-color:#ED164B;font-family:'NotoSansKR_B', sans-serif;font-size:12px;line-height:14px;color:#fff;box-sizing:border-box;text-align:center}
.icon_point{display:block;width:6px;height:6px;margin:10px auto 0;border-radius:100%;background-color:#ED164B}
.icon_point:after{position:absolute;top:25px;left:50%;width:2px;height:10px;margin-left:-1px;background-color:#ED164B;content:''}
</style>