<template>
  <div class="box_radio">
    <ul :class="radioListClass" ref="boxRadioList">
      <RadioItem
        v-for="(item, index) in dataList"
        :key="item.codeId || index"
        :name="name"
        :id="item.codeId || item.id"
        :text="item.codeName || item.text || item.name"
        :isDisabled="isDisabled || item.isDisabled" 
        v-model:selectedId="selectedIdSync"
        @update:selectedId="selectedId => $emit('selectItem', dataList.find(item => item.id == selectedId))" />
    </ul>
    <Input
      v-if="isDirectInput"
      :placeholder="placeholder"
      :isFocusOnMount="true"
      v-model:value="directValueSync" />
  </div>
</template>

<script>
import RadioItem from './RadioItem'
import Input from '@/components/common/input/Input';

import { mapState } from 'vuex';

export default {
  name: 'Radio',
  components: {
    RadioItem,
    Input
  },
  props: {
    dataList: Array,
    name: String,
    selectedId: undefined,
    directValue: undefined,
    isDisabled: {
      type: Boolean,
      default: false
    },
    placeholder:String,
  },
  mounted(){
    this.setRowHeight();
  },
  watch:{
    windowWidth(){
      this.setRowHeight();
    }
  },
  computed: {
    isDirectInput(){
      if(!this.selectedId) return false;
      const seleteditem = this.dataList.find(item => item.id == this.selectedId);
      return Boolean(seleteditem.isDirectInput);
    },
    selectedIdSync: {
      get() {
        return this.selectedId;
      },
      set(value) {
        this.$emit("update:selectedId", value);
      }
    },
    directValueSync: {
      get() {
        return this.directValue;
      },
      set(value) {
        this.$emit("update:directValue", value);
      }
    },
    radioListClass(){
      if( !this.dataList )  return '';

      var lengthValue = this.dataList.length;
      if(lengthValue >= 6){
        lengthValue = '_multi';
      }
      
      return 'list_radio' + lengthValue;
    },
    ...mapState({
      windowWidth: state => state.viewport.windowWidth,
    }),
  },
  methods:{
    setRowHeight(){
      const childrenList = this.$refs.boxRadioList.children;
      if(childrenList && childrenList.length > 0){
        const childrenHeightList = Array.from(childrenList).map(item => item.children[1].children[0].offsetHeight);
        const childrenTopList = Array.from(childrenList).map(item => item.offsetTop);
        var targetIndexList = [];
        var maxHeightList = [];
        childrenHeightList.forEach((item, index) => {
          targetIndexList.push(index);
          maxHeightList.push(item);
          const isChangedRow = childrenTopList[index] != childrenTopList[index+1];
          if(isChangedRow){
            var maxHeight = Math.max(...maxHeightList)+2;
            targetIndexList.forEach(targetIndex => {
              childrenList[targetIndex].children[1].style.height = maxHeight+'px';
            })
            targetIndexList = [];
            maxHeightList = [];
          }
        });
      }
    },
  },
}
</script>
<style scope>
/* .box_radio{min-width:270px} */
.box_radio ul{overflow:hidden;border-radius:2px}
.box_radio li{float:left;position:relative;border:1px solid #e1e1e1;border-left:0 none;background-color:#fff;box-sizing:border-box}
.box_radio li:first-child{border-left:1px solid #e1e1e1;border-radius:2px 0 0 2px}
/* .box_radio li:first-child label:after{border-radius:2px 0 0 2px}
.box_radio li:last-child{border-radius:0 2px 2px 0}
.box_radio li:last-child label:after{border-radius:0 2px 2px 0} */
.box_radio .list_radio1 li{width:100%}
.box_radio .list_radio2 li{width:50%}
.box_radio .list_radio3 li{width:33.333%}
.box_radio .list_radio4 li{width:25%}

.box_radio .list_radio5 li{width:20%}
.box_radio_l .list_radio5 li{width:33.333%}
.box_radio_l .list_radio5 li:nth-child(3){border-radius:0 2px 2px 0}
.box_radio_l .list_radio5 li:nth-child(3) label:after{border-radius:0 2px 2px 0}
.box_radio_l .list_radio5 li:nth-child(4){border-left:1px solid #e1e1e1;border-radius:2px 0 0 2px}
.box_radio_l .list_radio5 li:nth-child(4) label:after{border-radius:2px 0 0 2px}
.box_radio_l .list_radio5 li:nth-child(4),
.box_radio_l .list_radio5 li:nth-child(5){border-top:0 none}

.box_radio .list_radio_multi li{width:33.333%}
.box_radio .list_radio_multi li:nth-child(3n+1){clear:left;border-left:1px solid #e1e1e1}
.box_radio .list_radio_multi li:nth-child(n+4){border-top:0 none}

.box_radio input[type="radio"]{position:absolute;top:0;bottom:0;left:0;right:0;z-index:10;width:100%;height:100%;border:0 none;opacity:0.01;filter:alpha(opacity=1);cursor:pointer}
.box_radio label{display:block;font-size:0;background-color:#fbfbfb;white-space:nowrap;text-align:center}
.box_radio label:before{display:none;position:absolute;top:-1px;bottom:-1px;left:-1px;right:-1px;border:1px solid #222;content:''}
.box_radio label:after{display:inline-block;height:100%;vertical-align:middle;content:''}
target{display:inline-block;vertical-align:middle;white-space:normal}
.box_radio .txt_radio{display:inline-block;max-width:100%;padding:9px 8px;font-size:12px;line-height:18px;color:#555;letter-spacing:-.05em;vertical-align:middle;white-space:normal;word-break:break-all}

.box_radio input[type="radio"]:checked + label{font-family:'NotoSansKR_M', sans-serif;background-color:#fff;color:#222}
.box_radio input[type="radio"]:checked + label:before{display:block}

.box_radio .tf_comm{margin-top:4px}
</style>