export default class HouseHoldInfoModel {
  constructor(tempName, id) {
    this.id = id;
    this.houseHoldName = tempName || ''; // 세대 이름
    this.dealType = []; // 거래
    this.saleStatus = ''; // 분양상태
    this.salePrice = null; // 분양가
    this.leaseAdjustYn = ''; // 전월세 조정여부
    this.leasePrice = null; // 전세가
    this.leaseRebate = null; // 전세R
    this.saleRebate = null; // 매매R
    this.acquisitionTaxSupportYn = ''; // 취득세 지원 여부
    this.acquisitionTaxSupport  =  null; // 취득세 지원
    this.monthlyRentDeposit = null; // 월세 보증금
    this.monthlyRentPrice = null; // 월세
    this.mortgageResident = ''; // 담보실입
    this.nonresidentYn = ''; // 무입여부
    this.holdingsName = ''; // 소유주이름
    this.buildingRegister = ''; // 건축물대장
    this.emptyRoomYn = ''; // 공실체크
    this.managerInfo = ''; // 담당자정보
    this.property = ''; // 소유권
    this.specialNote = ''; // 특이사항
    this.areaTypeId = null; // 타입선택Id
  }
}