export default class BuildingInfoModel {
  constructor(tempName, id) {
    this.id = id;
    this.buildingName = tempName || ''; // 동이름
    this.houseHoldsCount =  null; // 총 세대수
    this.groundFloor = null; // 지상 층수
    this.undergroundFloor = null; // 지하 층수
    this.elevatorYn = ''; // 엘리베이터 유무
    this.buildingType = []; // 건물형태
    this.parkingStyle = ''; // 주차방식
    this.suvParkingYn = ''; // SUV 주차여부
    this.parkingRatio = null; // 주차비율
    this.floorDataList = [ // 층별 정보
      {
        id : 0,
        floorName : '1층',
        houseHoldDataList : [] // 세대정보
      }
    ];
  }
}