<template>
  <li>
    <input 
      type="radio"
      :name="name"
      :disabled="isDisabled" 
      :id="id" 
      :value="id"
      v-model="input">
      <!-- :checked="checked" -->
    <label :for="id">
      <div class="txt_radio">{{ text }}</div>
    </label>
  </li>
</template>

<script>
export default {
  name: 'RadioItem',
  props: {
    id: undefined,
    text: String,
    name: String,
    selectedId: undefined,
    isDisabled: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    selected() {
      return this.id === this.selectedId
    },
    checked() {
      return this.selected ? 'checked' : null;
    },
    input: {
      get() {
        return this.selectedId;
      },
      set(value) {
        this.$emit("update:selectedId", value);
      }
    },
  },
}
</script>