<template>
  <div class="area_select">
    <div class="head_select">
      <strong class="tit_select">{{ title }}</strong>
    </div>
    <ul v-if="dataList.length > 0" class="list_select">
      <li v-for="(item, index) in dataList" :key="index" :class="{ active : selectedId == item.id }">
        <button 
          type="button"
          class="btn_type"
          @click="viewModel.onSelected(type, item.id)">
          <span
            v-if="viewModel.getAreaTypeName(item.areaTypeId)"
            :class="['badge_type','badge_type'+( item.areaTypeId+1 )]">
            {{ viewModel.getAreaTypeName(item.areaTypeId) }}
          </span>
          <span class="txt_type">{{ item[titleKeyName] }}</span>
        </button>
        <div
          v-if="(isEditMode || type != 'floor') || (isEditMode && type == 'floor')"
          class="area_util">
          <button
            v-if="isEditMode && !isCopyMode && type != 'floor'"
            type="button"
            class="btn_edit"
            @click="viewModel.onEdit(type,item.id)">
            <span class="ico_zippro ico_edit">편집</span>
          </button>
          <button
            v-if="isEditMode && !isCopyMode && dataList.length > 1"
            type="button"
            @click="onClickCopy(item)">
            <span class="ico_zippro ico_copy">복사</span>
          </button>
          <button
            v-if="isEditMode && !isCopyMode"
            type="button"
            @click="viewModel.onDelete(type,item.id)">
            <span class="ico_zippro ico_delete">삭제</span>
          </button>
          <button 
            v-if="( !isEditMode || isMobile ) && type != 'floor'"
            type="button"
            class="btn_detail"
            @click="viewModel.onDetail(type,item.id)">
            <span class="ico_zippro ico_detail">상세</span>
          </button>
          <CheckboxItem
            v-if="isCopyMode && viewModel.copyedData.id != item.id"
            :id="item"
            v-model:checkedNames="viewModel.selectedCopyData" />
        </div>
      </li>
    </ul>
    <p v-else class="desc_empty">{{ isSeletedUpper ? title+'를 등록해주세요' : upperName+'를 선택해주세요' }}</p>
    <div v-if="!isMobile && isSeletedUpper" class="util_select">
      <div class="area_right">
        <Button
          v-if="!isEditMode && dataList.length > 0"
          btnSize="small"
          btnStyle="tertiary"
          text="편집"
          @onClickBtn="onClickEdit()" />
        <Button
          v-if="!isEditMode"
          btnSize="small"
          btnStyle="tertiary"
          text="추가"
          @onClickBtn="viewModel.onAdd(type)" />
        <Button
          v-if="isEditMode"
          btnSize="small"
          btnStyle="secondary"
          text="전체삭제"
          @onClickBtn="viewModel.onDeleteAll(type)" />
        <Button
          v-if="isEditMode"
          btnSize="small"
          btnStyle="primary"
          text="완료"
          @onClickBtn="onClickComplete()" />
      </div>
    </div>
  </div>
</template>

<script>
import CheckboxItem from '@/components/common/checkbox/CheckboxItem';
import Button from '@/components/common/button/Button';

import { mapState } from 'vuex';

export default {
  name:'CategoryBoxList',
  components:{
    CheckboxItem,
    Button
  },
  props:{
    type: String,
    title: String,
    viewModel: Object,
  },
  data(){
    return{
      isMobile:false,
      isEditMode:false,
      isCopyMode:false,
    }
  },
  mounted(){
    this.setMobile();
  },
  watch:{
    windowWidth(){
      this.setMobile();
    }
  },
  computed:{
    upperName(){
      return this.viewModel.getUpperName(this.type);
    },
    isSeletedUpper(){
      return this.viewModel.getIsSeletedUpper(this.type);
    },
    titleKeyName(){
      return this.viewModel.getTitleKeyName(this.type);
    },
    dataList(){
      if(this.viewModel.getDataList(this.type).length == 0){
        this.isEditMode = false;
      }
      return this.viewModel.getDataList(this.type);
    },
    selectedId(){
      return this.viewModel[this.viewModel.getSelectedId(this.type)];
    },
    ...mapState({
      windowWidth: state => state.viewport.windowWidth,
    }),
  },
  methods:{
    setMobile(){
      if(this.windowWidth <= 768){
        this.isMobile = true;
        this.isEditMode = false;
      }else{
        this.isMobile = false;
      }
    },
    onClickCopy(item){
      this.isCopyMode = true;
      this.viewModel.copyedData = item;
    },
    onClickEdit(){
      this.isEditMode = true;
    },
    onClickComplete(){
      if( this.isCopyMode ){
        this.viewModel.onPaste(this.type)
        this.isCopyMode = false;
        this.isEditMode = false;
      }else if( this.isEditMode ){
        this.isEditMode = false;
      }
    }
  }
}
</script>
<style scoped>
.area_select{float:left;width:calc(50% - 95px);border-bottom:1px solid #e1e1e1;}
.area_select.area_select_floor{width:170px}
.area_select.area_select_floor .btn_type:only-child{width:100%}

.head_select .tit_select{display:block;padding:17px 0 16px;border-top:1px solid #222;border-bottom:1px solid #e1e1e1;font-weight:normal;font-family:'NotoSansKR_M', sans-serif;font-size:12px;line-height:16px;background-color:#FBFBFB;text-align:center}

.list_select{height:450px;overflow:hidden;overflow-y:auto;margin-bottom:-1px}
.list_select li{position:relative;padding:0 1px;border-bottom:1px solid #e1e1e1;box-sizing:border-box}
.list_select li.active{padding:0;border:1px solid #ED164B;background-color:#FFFAFC}
.list_select li + li.active{margin-top:-1px}
.list_select li.active:first-child .btn_type,
.list_select li.active:first-child .area_util button{padding-top:12px}
.list_select .btn_type{width:100%;padding:13px 10px;text-align:left}
.list_select .btn_type:hover .txt_type{text-decoration:underline}
.list_select .area_util{position:absolute;top:0;right:0;z-index:1}
.list_select .area_util button{display:inline-block;padding:13px 10px;vertical-align:top}
.list_select .area_util .check_comm:deep() label{width:43px;height:43px}
.list_select .area_util .check_comm:deep() .ico_checkbox{left:15px}

.desc_empty{height:449px;padding:216px 8px 0;background-color:#FFFAFC;font-size:12px;line-height:16px;color:#888;text-align:center;box-sizing:border-box}

.util_select{overflow:hidden;height:32px;border-top:1px solid #e1e1e1;padding:8px 0}
.util_select button + button{margin:0 0 0 4px}

/* 모바일 */
@media all and (max-width:768px){
  .area_select{width:calc(35% - 7px)}
  .area_select.area_select_floor{width:calc(30% - 7px)}
  .list_select{max-height:450px;height:auto}
  .list_select .btn_type:only-child{width:100%}
}
</style>