<template>
  <PageWithLayout>
    <div class="section_comm">
      <div class="head_section no_border">
        <h3 class="tit_section">신축 현장 등록</h3>
      </div>
      <div class="body_section no_border no_padding">
        <div class="inner_field_body">
          <Stepper
            style="max-width:400px"
            :dataList="viewModel.stepDataList"
            :currentStep="viewModel.currentStep"
            @onChangeStep="id => viewModel.onChangeFieldWriteStep(id)"/>

          <component
            :is="'NewField'+viewModel.currentStep.id"
            :viewModel="viewModel">
          </component>
        </div>
      </div>
    </div>

    <Sticky>
      <div class="area_right">
        <Button
          btnSize="large"
          btnStyle="primary"
          text="완료"
          :disabled="isInValidate"
          @onClickBtn="viewModel.newFieldInfoCompelete()" />
      </div>
    </Sticky>

    <template v-slot:popup>
      <NewFieldInfoPopup
        v-if="viewModel.isNewFieldInfoPopup"
        :type="viewModel.newFieldInfoPopupType"
        :viewModel="viewModel"/>
    </template>
  </PageWithLayout>
</template>

<script>
import PageWithLayout from '@/components/layout/PageWithLayout';
import Stepper from '@/components/common/stepper/Stepper';
import NewFieldInfoWrite from '@/views/field/view/components/NewFieldInfoWrite';
import NewFieldDetailWrite from '@/views/field/view/components/NewFieldDetailWrite';
import Sticky from "@/components/common/sticky/Sticky";
import Button from '@/components/common/button/Button';
//popup
import NewFieldInfoPopup from '@/views/field/view/popup/NewFieldInfoPopup';
// viewModel
import NewFieldViewModel from '@/views/field/viewModel/NewFieldViewModel';

export default {
  name:'FieldWrite',
  components:{
    PageWithLayout,
    Stepper,
    NewFieldInfoWrite,
    NewFieldDetailWrite,
    Sticky,
    Button,

    NewFieldInfoPopup
  },
  beforeMount(){
    this.viewModel.setModel();
  },
  data(){
    return{
      viewModel: new NewFieldViewModel(),
    }
  },
  computed:{
    isInValidate(){
      return !this.viewModel.validated();
    }
  },
}
</script>