<template>
  <Popup
    :title="title"
    :mode="viewModel.isPopupModifyMode"
    :closeBtnText="(viewModel.isDetailPopup && !viewModel.isPopupModifyMode) || !viewModel.isDetailPopup ? '닫기' : '취소'"
    :compeleteBtnText="!viewModel.isPopupModifyMode ? '수정' : '저장'"
    @onClickClose="viewModel.onClickCloseNewFieldInfoPopup(type)"
    @onClickComplete="viewModel.onClickCompleteNewFieldInfoPopup(type)">
    <template 
      v-for="(viewDataItem, index) in ViewDataList"
      :key="index" >
      <dl
        v-if="!viewDataItem.vIf || DataTarget[viewDataItem.vIf.valueName] == viewDataItem.vIf.value"
        :class="viewModel.isPopupModifyMode ? 'list_table list_table_w' : 'list_table list_table_v'">
        <dt>
          {{ viewDataItem.title }}
          <span
            v-if="viewModel.isPopupModifyMode && viewDataItem.isEssential"
            class="ico_zippro ico_star">
            필수항목
          </span>
        </dt>
        <dd v-if="viewModel.isPopupModifyMode">
          <component
            v-if="viewDataItem.componentName == 'Input' || 'InputUnit' || 'InputSelectNumber'"
            :is="viewDataItem.componentName"
            :unit="viewDataItem.unit"
            :placeholder="viewDataItem.placeholder"
            v-model:value="DataTarget[viewDataItem.value]">
          </component>
          <Radio
            v-if="viewDataItem.componentName == 'Radio'"
            :name="viewDataItem.name"
            :dataList="TypeViewModel[viewDataItem.dataList]"
            :placeholder="viewDataItem.placeholder"
            v-model:selectedId="DataTarget[viewDataItem.value]"
            v-model:directValue="DataTarget[viewDataItem.directValue]" />
          <BoxCheckbox
            v-if="viewDataItem.componentName == 'BoxCheckbox'"
            :dataList="TypeViewModel[viewDataItem.dataList]"
            v-model:checkedNames="DataTarget[viewDataItem.value]" />
        </dd>
        <dd v-else>{{ valueView(viewDataItem) }}</dd>
      </dl>
    </template>
  </Popup>
</template>

<script>
import Popup from '@/components/layout/popup/Popup';

import Input from '@/components/common/input/Input';
import InputUnit from '@/components/common/input/InputUnit';
import InputSelectNumber from '@/components/common/input/InputSelectNumber';

import Radio from '@/components/common/radio/Radio';
import BoxCheckbox from '@/components/common/checkbox/BoxCheckbox';

import { addCommaForMoney } from '@/utils/numberUtils'

export default {
  name: 'NewFieldInfoPopup',
  components:{
    Popup,
    Input,
    InputUnit,
    InputSelectNumber,
    Radio,
    BoxCheckbox
  },
  props:{
    viewModel: Object,
    type:String,
  },
  mounted(){
    if(this.type == 'houseHold'){
      this.setAreaTypeDataList();
    }
  },
  computed:{
    title(){
      return this.viewModel.getTypeName(this.type) + '정보';
    },
    TypeViewModel(){
      switch(this.type){
        case 'areaType':{
          return this.viewModel.AreaTypeInfoViewModel;
        };
        case 'building':{
          return this.viewModel.BuildingInfoViewModel;
        };
        case 'houseHold':{
          return this.viewModel.HouseHoldInfoViewModel;
        };
      }
    },
    ViewDataList(){
      switch(this.type){
        case 'areaType':{
          return this.TypeViewModel.areaTypeInfoView;
        };
        case 'building':{
          return this.TypeViewModel.buildingInfoView;
        };
        case 'houseHold':{
          return this.TypeViewModel.houseHoldInfoView;
        };
      }
    },
    DataTarget(){
      switch(this.type){
        case 'areaType':{
          return this.viewModel.areaTypeInfoData;
        };
        case 'building':{
          return this.viewModel.buildingInfoData;
        };
        case 'houseHold':{
          return this.viewModel.houseHoldInfoData;
        };
      }
    }
  },
  methods:{
    setAreaTypeDataList(){
      var areaTypeDataList = [];
      areaTypeDataList = areaTypeDataList.concat(this.viewModel.model.areaTypeDataList)
      const dataList = areaTypeDataList.map(item => {
        var obj = {};
        obj.id = item.id;
        obj.text = item.areaTypeName;
        return obj;
      });
      this.TypeViewModel.areaTypeDataList = dataList;
    },
    valueView(viewDataItem){
      var value = this.DataTarget[viewDataItem.value];
      if(String(value) == '' || value == null || value.length == 0) return '-';
      const type = typeof(value);
      if( viewDataItem.dataList ){
        var dataList = JSON.parse(JSON.stringify(this.TypeViewModel[viewDataItem.dataList]));
        if(Array.isArray(value)){
          value = JSON.parse(JSON.stringify(value));
          const valueDataList = dataList.filter(item => value.includes(item.id)).map(item => item.text);
          return valueDataList.join(', ');
        }else{
          return dataList.find(item => item.id == value).text;
        }
      }
      switch(type){
        case 'string':{
          return `${value}${viewDataItem.unit ? viewDataItem.unit : ''}`;
        }
        case 'number':{
          return `${addCommaForMoney(value)}${viewDataItem.unit ? viewDataItem.unit : ''}`;
        }
      }
    }
  }
}
</script>