import { getMoneyToNumber } from '@/utils/stringUtils';

export default class  NewFieldModel {
  constructor(){
      // 신축 현장 등록
    this.fieldName = ''; // 현장이름
    this.fieldAddress = ''; // 주소
    this.buildingCount = null; // 동(건물)수
    this.areaTypeCount = null; // 총 타입수
    this.houseHoldsCount = null; // 총 세대수
    this.fieldOpenDate = ''; // 현장 오픈 예정일
    this.completionDate = ''; // 준공 예정일
    this.ceoName = ''; // 대표자 이름
    this.ceoPhone = ''; // 대표자 전화번호
    this.managerInfoList = []; // 실무자 리스트
    this.request = ''; // 요청사항
    // 타입정보
    this.areaTypeDataList = [];
    // 동정보
    this.buildingInfoDataList = [];
    // // 층정보
    // this.floorDataList = [];
    // //세대정보
    // this.houseHoldDataList = [];
  }

  setData(obj){
    const { fieldName, fieldAddress, buildingCount, areaTypeCount, houseHoldsCount, fieldOpenDate, completionDate, ceoName, ceoPhone, managerInfoList, request } = obj;

    if (fieldName) this.fieldName = fieldName;
    if (fieldAddress) this.fieldAddress = fieldAddress;
    if (buildingCount) this.buildingCount = buildingCount;
    if (areaTypeCount) this.areaTypeCount = areaTypeCount;
    if (houseHoldsCount) this.houseHoldsCount = houseHoldsCount;
    if (fieldOpenDate) this.fieldOpenDate = fieldOpenDate;
    if (completionDate) this.completionDate = completionDate;
    if (ceoName) this.ceoName = ceoName;
    if (ceoPhone) this.ceoPhone = ceoPhone;
    if (managerInfoList) this.managerInfoList = managerInfoList;
    if (request) this.request = request;
  }

  getData(){
    let obj = {
      fieldName : this.fieldName,
      fieldAddress : this.fieldAddress,
      buildingCount : getMoneyToNumber(this.buildingCount),
      areaTypeCount : getMoneyToNumber(this.areaTypeCount),
      houseHoldsCount : getMoneyToNumber(this.houseHoldsCount),
      fieldOpenDate : this.fieldOpenDate,
      completionDate : this.completionDate,
      ceoName : this.ceoName,
      ceoPhone : this.ceoPhone,
      managerInfoList : this.managerInfoList,
      request : this.request,
    }
    return obj;
  }
}