export default class AreaTypeInfoModel {
  constructor(tempName,id) {
    this.id = id;
    this.areaTypeName = tempName || ''; // 타입명
    this.fieldType = ''; // 등기정보
    this.roomSize = null; // 전용면적
    this.provisionSize = null; // 공급면적
    this.actualSize = null; // 실평수
    this.roomCount = null; // 방갯수
    this.bathroomCount = null; // 화장실 갯수
    this.utilityroomCount = null; // 다용도실 갯수
    this.terraceCount = null; // 테라스 갯수
    this.yardCount = null; // 마당 갯수
    this.airConditionerCount = null; // 에어컨 갯수
    this.refrigeratorPlaceCount = null; // 냉장고 자리 갯수
    this.direction = ''; // 주택방향
    this.floorType = ''; // 바닥형태
    this.floorTypeDirect = ''; // 바닥형태 직접입력
    this.kitchenType = ''; // 거실주방
    this.kitchenStructure = ''; // 주방구조
    this.stepKitchenYn = ''; // 보조주방여부
    this.duplexTypeYn = ''; // 복층여부
    this.violateYn = ''; // 위반여부(불법확장)
    this.violateContent = ''; // 위반내용
    this.coolingFacilities = []; // 냉방시설
    this.cookingFacilities = []; // 조리시설
    this.livingFacilities = []; // 생활시설
    this.securityFacilities = []; // 보안시설
    this.option = []; // 세대옵션
    this.etcFacilities = []; // 기타시설
    this.specialNote = ''; // 특이사항
    this.photo = ''; // 사진정보
  }
}