<template>
  <div class="area_select">
    <div class="head_select">
      <strong class="tit_select">{{ title }}</strong>
      <div v-if="!isMobile" class="util_select">
        <Button
          v-if="!isEditMode && dataList.length > 0"
          btnSize="small"
          btnStyle="tertiary"
          text="편집"
          @onClickBtn="onClickEdit()" />
        <Button
          v-if="!isEditMode"
          btnSize="small"
          btnStyle="tertiary"
          text="추가"
          @onClickBtn="viewModel.onAdd(type)" />
        <Button
          v-if="isEditMode"
          btnSize="small"
          btnStyle="secondary"
          text="전체삭제"
          @onClickBtn="viewModel.onDeleteAll(type)" />
        <Button
          v-if="isEditMode"
          btnSize="small"
          btnStyle="primary"
          text="완료"
          @onClickBtn="onClickComplete()" />
      </div>
    </div>
    <ul v-if="dataList.length > 0" class="list_select">
      <li v-for="(item, index) in dataList" :key="index">
        <button
          type="button"
          class="btn_type"
          @click="!isMobile && isEditMode ? viewModel.onSelected(type, item.id) : viewModel.onDetail(type,item.id)">
          <span class="txt_type">{{ item[titleKeyName] }}</span>
        </button>
        <div
          v-if="isEditMode || isMobile"
          class="area_util">
          <button
            v-if="isEditMode && !isCopyMode"
            type="button"
            @click="viewModel.onEdit(type,item.id)">
            <span class="ico_zippro ico_edit">편집</span>
          </button>
          <button
            v-if="isEditMode && !isCopyMode && dataList.length > 1"
            type="button"
            @click="onClickCopy(item)">
            <span class="ico_zippro ico_copy">복사</span>
          </button>
          <button
            v-if="isEditMode && !isCopyMode"
            type="button"
            @click="viewModel.onDelete(type,item.id)">
            <span class="ico_zippro ico_delete">삭제</span>
          </button>
          <CheckboxItem
            v-if="isCopyMode && viewModel.copyedData.id != item.id"
            :id="item"
            v-model:checkedNames="viewModel.selectedCopyData" />
        </div>
      </li>
    </ul>
    <p v-else class="desc_empty">{{ title }}를 등록해주세요</p>
  </div>
</template>

<script>
import CheckboxItem from '@/components/common/checkbox/CheckboxItem';
import Button from '@/components/common/button/Button';

import { mapState } from 'vuex';

export default {
  name:'CategoryBoxList',
  components:{
    CheckboxItem,
    Button
  },
  props:{
    type: String,
    title: String,
    viewModel: Object,
  },
  data(){
    return{
      isMobile:false,
      isEditMode:false,
      isCopyMode:false,
    }
  },
  mounted(){
    this.setMobile();
  },
  watch:{
    windowWidth(){
      this.setMobile();
    }
  },
  computed:{
    titleKeyName(){
      return this.viewModel.getTitleKeyName(this.type);
    },
    dataList(){
      if(this.viewModel.getDataList(this.type).length == 0){
        this.isEditMode = false;
      }
      return this.viewModel.getDataList(this.type);
    },
    selectedId(){
      return this.viewModel[this.viewModel.getSelectedId(this.type)];
    },
    ...mapState({
      windowWidth: state => state.viewport.windowWidth,
    }),
  },
  methods:{
    setMobile(){
      if(this.windowWidth <= 768){
        this.isMobile = true;
        this.isEditMode = false;
      }else{
        this.isMobile = false;
      }
    },
    onClickCopy(item){
      this.isCopyMode = true;
      this.viewModel.copyedData = item;
    },
    onClickEdit(){
      this.isEditMode = true;
    },
    onClickComplete(){
      if( this.isCopyMode ){
        this.viewModel.onPaste(this.type)
        this.isCopyMode = false;
        this.isEditMode = false;
      }else if( this.isEditMode ){
        this.isEditMode = false;
      }
    }
  },
}
</script>
<style scoped>
.area_select{border-bottom:1px solid #e1e1e1}

.head_select{overflow:hidden;padding-bottom:8px;border-bottom:1px solid #222}
.head_select .tit_select{display:inline-block;padding:8px 0 4px;font-weight:normal;font-family:'NotoSansKR_M', sans-serif;font-size:16px;line-height:18px;vertical-align:top}
.head_select .util_select{float:right}
.head_select .util_select button + button{margin:0 0 0 4px}

.list_select{margin-right:-8px;padding-bottom:8px}
.list_select li{display:inline-block;width:142px;margin:8px 8px 0 0;border:1px solid #e1e1e1;font-size:14px;line-height:20px;vertical-align:top;text-align:center;box-sizing:border-box}
.list_select .btn_type{width:100%;padding:14px 12px 0 20px}
.list_select .btn_type:only-child{height:74px;padding-bottom:14px}
.list_select .btn_type:hover .txt_type{text-decoration:underline}
.list_select .txt_type{position:relative;padding-left:10px}
.list_select .txt_type:after{position:absolute;top:7px;left:-4px;display:block;width:8px;height:8px;border-radius:8px;background-color:#F8626C;content:''}
.list_select li:nth-child(1) .txt_type:after{background-color:#FBC02E}
.list_select li:nth-child(2) .txt_type:after{background-color:#82B1FF}
.list_select li:nth-child(3) .txt_type:after{background-color:#8BC34A}
.list_select li:nth-child(4) .txt_type:after{background-color:#EC407A}
.list_select li:nth-child(5) .txt_type:after{background-color:#78909C}
.list_select li:nth-child(6) .txt_type:after{background-color:#FF7043}
.list_select li:nth-child(7) .txt_type:after{background-color:#3F51B4}
.list_select li:nth-child(8) .txt_type:after{background-color:#795548}
.list_select li:nth-child(9) .txt_type:after{background-color:#388E3B}
.list_select li:nth-child(10) .txt_type:after{background-color:#8E24AA}
.list_select li.active{border:1px solid #ED164B;background-color:#FFFAFC}
.list_select .area_util{height:38px;margin-top:4px}
.list_select .area_util button{display:inline-block;padding:10px 10px;vertical-align:top}
.list_select .area_util .check_comm:deep() label{width:38px;height:38px}
.list_select .area_util .check_comm:deep() .ico_checkbox{left:12px}

.desc_empty{height:90px;background-color:#FFFAFC;font-size:12px;line-height:90px;color:#888;text-align:center}

/* 모바일 */
@media all and (max-width:768px){
  .list_select li{width:calc(50% - 8px)}
  .list_select .btn_type{padding:12px 10px 8px 18px}
  .list_select .txt_type:after{top:6px}
  .list_select .area_util{float:right}
}
</style>